.NavigationBackground {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  z-index: -1;
  -webkit-transition: opacity .2s linear;
  transition: opacity .2s linear; }

.NavigationHeader {
  padding: 10px;
