  -webkit-transition: background-color .2s linear, color .2s linear;
  transition: background-color .2s linear, color .2s linear; }

.NavigationSectionList {
  float: left;
  max-width: 180px;
  margin-right: 15px;
  background-color: white;
  margin-bottom: 15px;
  z-index: 1000; }

.NavigationSection {
  display: block; }

.NavigationLink {
  padding: 10px;
